.project {
	padding: 10px 0 20px;
	margin: 20px 0 80px;
	background-color: #ebf7f6;


	&_container {
		@include container(false);
		position: relative;

		&::before {
			content: "";
			position: absolute;
			background: url("../img/letter_A.png") no-repeat;
			width: 369px;
			height: 457px;
			top: -75px;
			right: -100px;
			display: none;
		}
	}

	&_title {
		font-family: Georgia, serif;
		font-size: 22px;
		color: #4f4b4b;
		letter-spacing: .75px;
	}

	&_description {
		p {
			letter-spacing: .75px;
		    line-height: 1.5;
		}
	}

	&_video {
		text-align: center;
	}

	&_video-toggler {
		display: inline-block;
		vertical-align: middle;
		position: relative;
		box-shadow: -10px -10px 68px 6px rgba(0,0,0,0.25);;

		img {
			max-width: 100%;
		}

		&::after {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 84px;
			height: 84px;
			border-radius: 50%;
			background: url('data:image/png;base64,');
			background-repeat: no-repeat;
			background-position: center;
			background-color: rgba(255,255,255,.8);
			border: 16px solid rgba($primary-color, .2);
			transition: .2s;
			cursor: pointer;
		}

		&:hover,
		&:focus {
			&::after {
				border-color: rgba($primary-color, .7);
				background-color: rgba(255,255,255,1);
			}
		}
	}

	&_link {
		text-align: right;
		font-size: 16px;
	}

	@media screen and (min-width: 768px) {
		&_container {
			display: flex;

			&::before {
				display: block;
				top: -75px;
				right: 0px;
				left: auto;
				bottom: auto;
				transform: none;
			}
		}

		&_description {
			max-width: 300px;
		}

		&_video {
			flex-grow: 1;
			flex-shrink: 1;
			padding: 0 10px;
		}

		&_link {
			font-size: 18px;
		}
	}

	@media screen and (min-width: 1280px) {
		&_container {
			&::before {
				top: -75px;
				right: -100px;
			}
		}
	}
}